define(["exports", "jquery", "mod_lanebs/modal_video_handle"],
    function (exports, $, ModalVideoHandle) {
        return {
            init: function(title) {
                let VIDEO_BUTTON_SELECTOR = '[data-action="video_modal"]';
                $(VIDEO_BUTTON_SELECTOR).on('click', function(e) {
                    ModalVideoHandle.init(title);
                });
            }
        };
    });